* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.App {
    width: 100%;
    background-image: url('./images/bac.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: auto;
}
.nav {
    position: fixed;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    list-style: none;
    background-color: #000080;  
    width: 100%;
    padding-top: 1.5px;
    padding-left: 20px;
}
.nav li {
    margin-right: 20px;         
}
.home {
    margin-right: 8px;
}
a {
    text-decoration: none;
    color: #ffffff;
}
a:hover {
    text-decoration: underline;
    cursor: pointer;
}
.translate {
   position: fixed;
   background-color: #000080;
   padding-left: 20px;
   padding-right: 50px;
}
a.gflag {    
    vertical-align:middle;
    font-size:16px;
    padding:1px 0;
    background-repeat:no-repeat;
    background-image:url(//gtranslate.net/flags/16.png);
}
a.gflag img {
    border:0;
}
a.gflag:hover {
    background-image:url(//gtranslate.net/flags/16a.png);
}
#goog-gt-tt {
    display:none !important;
}
.goog-te-banner-frame {
    display:none !important;
}
.goog-te-menu-value:hover {
    text-decoration:none !important;
}
body {
    top:0 !important;
}
#google_translate_element2 {
    display:none!important;
}
.logoImg {
    width: 100%;
    max-width:  80px;
    margin-left: 5%;    
}
.header {
    position: fixed;
    width: 100%;
    height: auto;
    background-color: rgba(0,0,0,.54);
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: nowrap;
    -moz-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    -o-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -ms-align-items: center;
    align-items: center;
    margin-top: 20px;
    z-index: 200;
}
.search {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    width: 100%;
}
.select {
    width: 30%;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    font-size: 18px;
    margin-left: 5%;
}
h2 {
    font-weight: 300;
    font-size: 24px;
}
.divh2 {
    border-bottom: 1px solid #ffffff;
    margin-bottom: 10px;
}
.main {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    color: #ffffff;
    font-size: 18px; 
    padding-top: 115px;
    padding-right: 20px;
    padding-left: 20px;
    padding-bottom: 20px;
}
.myLocations {
   width: 14%;
   height: auto;
}
.myLocations, 
.generalInformationCity,
.forecast,
.precipitation,
.windPressure,
.details,
.map {
    background-color: rgba(0,0,0,.54); 
    padding: 10px;
    margin-bottom: 15px;
}
.generalInformationCity,
.forecast,
.precipitation,
.windPressure,
.details,
.map {
    margin-left: 15px;   
}
.content {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
    width: 100%;
}
.favorit {
    margin-bottom: 10px;;
}
.btnLoc{
    width: 100%;
    height: 20px;
    margin: 10px auto;   
}
.citiesList {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
}
.linkWorld {
    margin-bottom: 8px;
}
.linkWorld:hover {
    text-decoration: underline;
    cursor: pointer;
}
.contentDetail {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: nowrap;
    -moz-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    -o-flex-wrap: nowrap;
    flex-wrap: nowrap;
    align-items: stretch;
}
.sec1, 
.sec2 {
    width: 100%;
}
.sec2 {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
    align-content: space-between;
}
.city, 
.country,
.dateTime,
.weatherCharacter,
.tempHighLow,
.temperatureIsNow {
    margin-bottom: 10px;
}
.city {
    font-size: 60px;
}
.weatherCharacter {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -ms-align-items: center;
    align-items: center;
    font-size: 30px;
}
.weatherCharacter img {
    width: 100%;
    max-width: 150px;
    height: auto;
    margin-right: 20px;
}
.tempHigh,
.tempLow {
    font-size: 22px;
    margin-right: 20px;
}

.tempHigh i,
.tempLow i {
    margin-right: 12px;
}
.temperatureIsNow {
    font-size: 140px;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex; 
    -ms-align-items: flex-start;
    align-items: flex-start;
}
.tempIsNowSpan {
    font-size: 80px;    
}
.dateTime:first-letter {
    text-transform: uppercase;
}
.tempHighLow {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    -ms-align-items: flex-start;
    align-items: flex-start;
}
.forecastDay {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    justify-content: space-around;
    -ms-align-items: center;
    align-items: center;
}
.day,
.forImg,
.forText,
.forHigh,
.forLow {
    text-align: left;
}
.day {
    width: 35%;
    text-align: left;
}
.forImg {
   width:120px;
}
.forText {
    text-align: left;
    width: 20%;
}
.forHigh,
.forLow {
    text-align: right;
    width: 10%;    
}
.detAll {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    -ms-align-items: center;
    align-items: center;    
}
.detImg {
    width: 33%;
    text-align: center;
}
.detImg img {
    width: 100%;
    max-width: 250px;
}
.detMain {
    width: 66%;
}
.feelsLike,
.humidity,
.visibility,
.indexUV,
.sunrise,
.sunset {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    justify-content: space-between;
    -ms-align-items: center;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 18px;
}
.windPressureContent {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;

}
.iconWind {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    -ms-align-items: flex-start;
    align-items: flex-start;
    position: relative;
}
@keyframes rotat {
  100% {
   transform: rotate(360deg);
  }  
}
.trsdu {
    width: 100%;
    max-width: 60px;
    background-image:url(https://s.yimg.com/os/weather/1.0.1/windmill/blade_big@2x.png);
    background-repeat: no-repeat;
    background-size: cover;
    height: 60px;    
    animation: rotat 2.25s infinite linear; 
}
.trsdu1 {
    position: absolute;
    margin-top: 30px;
    margin-left: 20px;
    max-width: 20px;
    width: 100%;
    height: 100%;
    max-height: 80px;
    background-image:url(https://s.yimg.com/os/weather/1.0.1/windmill/bigpole@2x.png);
    background-repeat: no-repeat;    
}
.trsduSmall {
    width: 100%;
    max-width: 40px;
    background-image:url(https://s.yimg.com/os/weather/1.0.1/windmill/blade_big@2x.png);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    max-height: 40px;
    animation: rotat 2.25s infinite linear;
    margin-top: 40px; 
} 
.trsduSmall1 {
    position: absolute;
    margin-top: 60px;
    margin-left: 70px;
    width: 20px;
    height: 50px;
    background-image:url(https://s.yimg.com/os/weather/1.0.1/windmill/bigpole@2x.png);
    background-repeat: no-repeat;    
}
.iconWind {
    width: 33%;
}
.windPressureValue {
    width: 66%;
}
.wind {
    text-align: left;
    padding-left: 20px;
    padding-top: 10px;
    width: 100%;
}
.pressure {
    text-align: right;
    -ms-align-self: flex-end;
    align-self: flex-end;
    padding-top: 20px;
    width: 100%;
}
.map {
    flex-grow: 4;
}
.loadingElement, 
.containerElement, 
.mapElement {
    height: 312px;
}

.myLocationsSearch {
    padding-top: 115px;
   width: 100%;
   height: auto;
}
.favoritSearch {
    margin-bottom: 10px;;
}
.favoritSearch>h2, .myLocationsSearch p  {
text-align: center;
}
.listCity {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    -o-flex-direction: row;
    flex-direction: row;
    justify-content: flex-start;
}
.btnSearch  {
    width: 50%;
    height: 100vh;
}
.citiesListSearch {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
    width: 50%;
}
.btnLocSearch {
    width: 100%;
    height: 20px;
    margin: 10px auto;   
}
.linkWorldSearch {
    margin-bottom: 8px;
} 
.linkWorldSearch:hover {
    text-decoration: underline;
    cursor: pointer;
}
.divh2Search {
    width: 60%;
    border-bottom: 1px solid #ffffff;
    margin-bottom: 10px;
    margin-left: 20px;
}


@media only screen and (max-width: 900px) {
    .contentDetail {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        -webkit-flex-direction: column;
        -moz-flex-direction: column;
        -ms-flex-direction: column;
        -o-flex-direction: column;
        flex-direction: column;
        -webkit-flex-wrap: nowrap;
        -moz-flex-wrap: nowrap;
        -ms-flex-wrap: nowrap;
        -o-flex-wrap: nowrap;
        flex-wrap: nowrap;
    }
    .sec2  {
       order: 1;
    }
    .sec1  {
       order: 2;
    }
    .myLocations {
       width: 25%;
       height: auto;
    }
}

@media only screen and (max-width: 540px) {
    .generalInformationCity,
    .forecast,
    .precipitation,
    .windPressure,
    .details,
    .map {
        margin-left: 0;      
    }
    .myLocations {
      display: none;
    }
    .select {
        width: 50%;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 20px;
        font-size: 18px;
        margin-left: 3%;
    }
    .search {
        margin-right: 2%;
    }    
} 

@media only screen and (max-width: 400px) {
    .search {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        -webkit-flex-direction: column;
        -moz-flex-direction: column;
        -ms-flex-direction: column;
        -o-flex-direction: column;
        flex-direction: column;
    }
    .select {
        width: 75%;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 20px;
        font-size: 18px;
        margin-left: 0;
    }
    .main {
        padding-top: 155px;
    }
    .city {
        font-size: 50px;
    }
    .temperatureIsNow {
        font-size: 120px;    
    }
    .forecast,    
    .windPressure {
        font-size: 12px;
    }
    .feelsLike,
    .humidity,
    .visibility,
    .indexUV,
    .sunrise,
    .sunset {
     font-size: 12px;
    }
    .iconWind {
        width: 50%;
    }
    .windPressureValue {
        width: 50%;
    }
    .wind {
        text-align: left;
        padding-left: 20px;
        padding-top: 10px;
        width: 100%;
    }
    .pressure {
        text-align: right;
        -ms-align-self: flex-end;
        align-self: flex-end;
        padding-top: 40px;
        width: 100%;
    }    
} 

